import React, { Component } from 'react';
import {CardList} from './components/card-list/card-list.component';
import {SearchBox} from "./components/search-box/search-box.component";
import './App.css';


//class based component
//code it ben, let's make it learning react js before 2021
class App extends Component{

 //lifecycle method
  componentDidMount(){
    const usersURI = `https://jsonplaceholder.typicode.com/users`;
    fetch(usersURI)
    .then(resp => resp.json())
    .then(da => this.setState({mozilla:da}))
  }

  constructor(){
    super();
    this.state = {
        mozilla : [],
        searchField : " "   
    }
    //this.callB = this.callB.bind(this);
  }
   callB = (e) => {
    //console.log(this);
    return  this.setState({ searchField : e.target.value });
   }

  // callB(e){
  //   console.log(this);
  //   return  this.setState({ searchField : e.target.value });
  // }
  
  render(){
    const {mozilla, searchField} = this.state;
    const filtereSearchRes = mozilla.filter( single => single.name.toLowerCase().includes(searchField.toLowerCase())

    )
    return(
      <div  >
        <h1>Hello world , and this is built on react version : {React.version} {this.state.string1}</h1>
        <div className="container">
          <SearchBox  placeholder='search..'  handleInput={this.callB} />
        </div>
        <button onClick={() => this.setState({string1: Math.floor(Math.random() * 10)} ,() => console.log(this.state))} >Click me</button>
    


      
      <CardList chunk={filtereSearchRes}></CardList>

      Built with React version {React.version}
      </div>
    )
  }
}
export default App;
